/**
 *  来电渠道偏好 mixin module
 *  @module base/mixin/incomingClient
 *  @author coder
 */
import {
  themeLabelDisplay,
  labelUserDisplay,
  channelRegUser,
  channelUserUse,
  channelBusiness,
  channelNotCompleteBusiness,
  channelDetailedList
} from '@/base/api/incoming-client'

// mixin
export default {

  /**
   * 来电渠道偏好数据存储载体对象
   * @member incomingClient
   * @property {Array} [list=[]] 获取来电渠道偏好列表响应数据
   * @property {number} [page=null] 获取来电渠道偏好列表响应页码
   * @property {number} [limit=null] 获取来电渠道偏好列表页大小，每页记录条数
   * @property {number} [total=0] 获取来电渠道偏好列表数据记录总条数
   * @property {object} [model=null] 获取来电渠道偏好单条记录
   */
  data() {
    return {
      incomingClient: {
        list: [],
        page: null,
        limit: null,
        total: 0,
        model: null
      }
    }
  },
  methods: {

    /**
     * 主题标签-标签展示
     * @function themeLabelDisplay
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    themeLabelDisplay(data) {
      return themeLabelDisplay(data).then(res => {
        return res
      })
    },
    /**
     * 主题标签-通过标签查询覆盖用户信息
     * @function labelUserDisplay
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    labelUserDisplay(data) {
      return labelUserDisplay(data).then(res => {
        return res
      })
    },
    /**
     * 各渠道注册用户
     * @function channelRegUser
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    channelRegUser(data) {
      return channelRegUser(data).then(res => {
        return res
      })
    },
    /**
     * 各渠道注册用户使用情况
     * @function channelUserUse
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    channelUserUse(data) {
      return channelUserUse(data).then(res => {
        return res
      })
    },
    /**
     * 各渠道各类业务办理情况
     * @function channelBusiness
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    channelBusiness(data) {
      return channelBusiness(data).then(res => {
        return res
      })
    },
    /**
     * 各渠道未完结业务办理情况
     * @function channelNotCompleteBusiness
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    channelNotCompleteBusiness(data) {
      return channelNotCompleteBusiness(data).then(res => {
        return res
      })
    },
    /**
     * 各渠道注册用户使用情况-渠道明细
     * @function channelDetailedList
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    channelDetailedList(data) {
      return channelDetailedList(data).then(res => {
        return res
      })
    }
  }
}
