<template>
  <div class="panoramicAnalysis">
    <div class="top">
      <div class="content">
        <div class="content-left content-border">
          <div class="content-title">
            <span class="block"></span>
            <span>各渠道注册用户</span>
          </div>
          <div class="chart-main">
            <pieCharts
              v-if="registeredUsersRes"
              :pieChartsData="registeredUsersData"
              :roseType="false"
              :labelDw="'%'"
              :radius="['30%', '70%']"
              :name="'各渠道注册用户'"
              @setOption="setOption"
            ></pieCharts>
          </div>
        </div>
        <div class="content-right content-border">
          <div class="content-title">
            <span class="block"></span>
            <span>各渠道用户使用情况</span>
          </div>
          <div class="chart-main">
            <barCharts
              v-if="useUsageRes"
              :barChartsData="useUsageData"
              :barWidth="40"
              :barType="'bar'"
              @clickBar="getUseUsageDetailData"
              :showDetail="true"
              :showDetailData="useUsageDetailData"
              @changePageData="changePage"
              :name="'各渠道用户使用情况'"
              @setOption="setOption"
              @exportExcel="detailListExportExcel"
            ></barCharts>
          </div>
        </div>
      </div>
    </div>
    <div class="center">
      <div class="content">
        <div class="content-left content-border">
          <div class="content-title">
            <span class="block"></span>
            <span>各渠道各类业务办理情况</span>
          </div>
          <div class="chart-main">
            <barCharts
              v-if="businessManagementRes"
              :barChartsData="businessManagementData"
              :legendStatus="true"
              :isMultiple="true"
              :isShadow="true"
              :barWidth="20"
              :barType="'bar'"
              :name="'各渠道各类业务办理情况'"
              @setOption="setOption"
            ></barCharts>
          </div>
        </div>
        <div class="content-right content-border">
          <div class="content-title">
            <span class="block"></span>
            <span>各渠道未完结业务办理情况</span>
          </div>
          <div class="chart-main">
            <barCharts
              v-if="unfinishedBusinessRes"
              :barChartsData="unfinishedBusinessData"
              :legendStatus="true"
              :isMultiple="true"
              :isShadow="true"
              :barWidth="20"
              :barType="'bar'"
              :name="'各渠道未完结业务办理情况'"
              @setOption="setOption"
            ></barCharts>
          </div>
        </div>
      </div>
    </div>

    <div class="bottom">
      <div class="content content-border">
        <div class="content-title">
          <span class="block"></span>
          <span>标签分布</span>
        </div>
        <div class="chart-main">
          <themeLabelList
            :labelData="labelData"
            :pageConfig="labelPageConfig"
            @changePage="changePage"
            :showDetailData="labelDetailData"
            @clickDetail="getLabelDetailData"
            @changeDetailPage="changePage"
            @exportExcel="themeLabelExportExcel"
          ></themeLabelList>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Interface from '@/base/mixin/incoming-client';
import barCharts from "../../components/bar-charts/bar-charts";
import pieCharts from "../../components/pie-charts/pie-charts";
import themeLabelList from "@/components/theme-label-list/theme-label-list";
import { exportExcelGet } from "@/utils/export";

export default {
  components: {
    barCharts,
    pieCharts,
    themeLabelList
  },
  name: "panoramicAnalysis",
  mixins: [Interface],
  data() {
    return {
      //策略推荐时间
      strategyTime: [],
      //各渠道注册用户
      registeredUsersData: {},
      registeredUsersRes: false,
      //各渠道用户使用情况
      useUsageData: {},
      useUsageRes: false,
      useUsageDetailData: {},
      useUsageDetailQueryForm: {
        channel: "",
        current: 1,
        size: 10,
      },
      useUsageDetailPageConfig: {
        name: "useUsageDetail",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //各渠道各类业务办理情况
      businessManagementData: {},
      businessManagementRes: false,
      //各渠道未完结业务办理情况
      unfinishedBusinessData: false,
      unfinishedBusinessRes: false,
      //关注标签
      labelData: [],

      labelQueryForm: {
        menuType: "2",
        current: 1,
        size: 10
      },
      //分页设置
      labelPageConfig: {
        name: "label",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //标签详情
      labelDetailData: {},
      labelDetailQueryForm: {
        dbcolumnAS: "",
        menuType: "2",
        tagName: "",
        current: 1,
        size: 10
      },
      //分页设置
      labelDetailPageConfig: {
        name: "labelDetail",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //图表组件数据
      setOptionData: {}
    };
  },
  mounted() {
    this.getRegisteredUsersData();
    this.getUseUsageData();
    this.getBusinessManagementData();
    this.getUnfinishedBusinessData();
    this.getLabelData();
    window.addEventListener("resize", () => {
      if (this.setOptionData["各渠道注册用户"])
        this.setOptionData["各渠道注册用户"].resize();
      if (this.setOptionData["各渠道用户使用情况"])
        this.setOptionData["各渠道用户使用情况"].resize();
      if (this.setOptionData["各渠道各类业务办理情况"])
        this.setOptionData["各渠道各类业务办理情况"].resize();
      if (this.setOptionData["各渠道未完结业务办理情况"])
        this.setOptionData["各渠道未完结业务办理情况"].resize();
    });
  },
  methods: {
    //图表组件
    setOption(name, val) {
      this.setOptionData[name] = val;
    },
    //改变分页
    changePage(data) {
      console.log(data, "data");
      if (data.name === "label") {
        this.labelQueryForm.size = data.size;
        this.labelQueryForm.current = data.current;
        this.getLabelData();
      }
      if (data.name === "labelDetail") {
        this.labelDetailQueryForm.dbcolumnAS = data.keyValue || "";
        this.labelDetailQueryForm.size = data.size;
        this.labelDetailQueryForm.current = data.current;
        this.getLabelDetailData();
      }
      if (data.name === 'useUsageDetail') {
        this.useUsageDetailQueryForm.size = data.size;
        this.useUsageDetailQueryForm.current = data.current;
        this.getUseUsageDetailData()
      }
    },
    //主题标签导出
    themeLabelExportExcel(data) {
      exportExcelGet(
        "/leanSeatingSkillController/exportLabelUserDisplay" +
        "?tagId=" +
        data.tagId
      );
    },
    //客户明细列表导出
    detailListExportExcel() {
      exportExcelGet(
        "/leanSeatingSkillController/exportExcelSeatDetails" +
        "?jn=" +
        this.seatNumebrDetailForm.name
      );
    },
    // 各渠道注册用户
    getRegisteredUsersData() {
      this.registeredUsersRes = false;
      this.channelRegUser({}).then(res => {
        let dataMap = []
        dataMap = res.map(e => {
          return {
            name: e.typeName,
            value: e.typeVaule,
          }
        })
        this.registeredUsersData = {
          dataMap: dataMap,
          colorMap: ["#1A84F4", "#08BEF3", "#1DE0BF", "#EAA01A", '#8663d5']
        };
        this.registeredUsersRes = true;
      })
    },
    // 各渠道用户使用情况
    getUseUsageData() {
      this.useUsageRes = false;
      this.channelUserUse({}).then(res => {
        let xNameData = []
        let dataMap = []
        res.forEach(e => {
          dataMap.push(e.typeCount)
          xNameData.push(e.typeName)
        });
        this.useUsageData = {
          dataMap: {
            '各渠道用户使用情况': dataMap
          },
          xNameData: xNameData,
          colorMap: {
            '各渠道用户使用情况': [
              // '#55BFFF', '#2790FF'
              ["#2790FF", "#55BFFF"],
              ["#19A7CD", "#66D6FF"],
              ["#19CDAE", "#77E7BE"],
              ["#29C644", "#8AE671"],
              ["#CC8A00", "#FACE8A"]
            ]
          },
          xyName: {
            x轴: { name: "", color: "#1A84F4", diffcolor: true },
            y轴: [{ name: "调用次数", color: "#808080" }]
          }
        };
        this.useUsageRes = true;
      })

    },
    // 获取渠道用户使用情况明细
    getUseUsageDetailData(data) {
      if (data) {
        this.useUsageDetailQueryForm.channel = data.name;
        this.useUsageDetailQueryForm.current = 1
        this.useUsageDetailQueryForm.size = 10
      }
      this.useUsageDetailData = {};
      this.channelDetailedList(this.useUsageDetailQueryForm).then(res => {
        this.useUsageDetailData = {
          tableData: [...res.list],
          headerColor: '#1899fa',
          title: '渠道明细列表',
          name: 'useUsageDetail',
          size: this.useUsageDetailQueryForm.size,
          current: this.useUsageDetailQueryForm.current,
          total: res.total,
          isExport: false,
          columnNameData: [
            { prop: "qdmc", label: "渠道名称" },
            { prop: "mbdxbh", label: "目标对象编号" },
            { prop: "mbdxmc", label: "目标对象名称" },
            { prop: "zcsj", label: "注册时间" },
            { prop: "dq", label: "地区" },
            { prop: "lxhm", label: "联系号码" }
          ]
        }
      })
    },
    //各渠道各类业务办理情况
    getBusinessManagementData() {
      this.businessManagementRes = false;
      this.channelBusiness({}).then(res => {
        let xNameData = []
        let dataMap1 = []
        let dataMap2 = []
        let dataMap3 = []
        res.forEach(e => {
          dataMap1.push(e.typeCounta)
          dataMap2.push(e.typeCountb)
          dataMap3.push(e.typeCountc)
          xNameData.push(e.typeName)
        });
        this.businessManagementData = {
          dataMap: {
            '过户': dataMap1,
            '更名': dataMap2,
            '改类': dataMap3
          },
          xNameData: xNameData,
          colorMap: {
            过户: [
              // '#55BFFF', '#2790FF'
              ["#2790FF", "#55BFFF", "#ddf2ff"]
            ],
            更名: [
              // '#55BFFF', '#2790FF'
              ["#19A7CD", "#66D6FF", "#dbf4fc"]
            ],
            改类: [
              // '#55BFFF', '#2790FF'
              ["#E69622", "#F8CC67", "#fdf3df"]
            ]
          },
          xyName: {
            x轴: { name: "", color: "#808080", diffcolor: false },
            y轴: [{ name: "次数", color: "#808080" }]
          }
        };
        this.businessManagementRes = true;
      })

    },
    //各渠道未完结业务办理情况
    getUnfinishedBusinessData() {
      this.unfinishedBusinessRes = false;
      this.channelNotCompleteBusiness({}).then(res => {
        let xNameData = []
        let dataMap1 = []
        let dataMap2 = []
        let dataMap3 = []
        res.forEach(e => {
          dataMap1.push(e.typeCounta)
          dataMap2.push(e.typeCountb)
          dataMap3.push(e.typeCountc)
          xNameData.push(e.typeName)
        });
        this.unfinishedBusinessData = {
          dataMap: {
            '过户': dataMap1,
            '更名': dataMap2,
            '改类': dataMap3
          },
          xNameData: xNameData,
          colorMap: {
            过户: [
              // '#55BFFF', '#2790FF'
              ["#2790FF", "#55BFFF", "#ddf2ff"]
            ],
            更名: [
              // '#55BFFF', '#2790FF'
              ["#19A7CD", "#66D6FF", "#dbf4fc"]
            ],
            改类: [
              // '#55BFFF', '#2790FF'
              ["#E69622", "#F8CC67", "#fdf3df"]
            ]
          },
          xyName: {
            x轴: { name: "", color: "#808080", diffcolor: false },
            y轴: [{ name: "次数", color: "#808080" }]
          }
        };
        this.unfinishedBusinessRes = true;
      })
    },

    // 获取关注标签
    getLabelData() {
      this.themeLabelDisplay(this.labelQueryForm).then(res => {
        this.labelPageConfig.total = res.total;
        this.labelPageConfig.size = this.labelQueryForm.size;
        this.labelPageConfig.current = this.labelQueryForm.current;
        this.labelData = res.list.map(e => {
          return {
            ...e,
            name: e.tagName,
            text: e.tagDescription,
            value: e.account || 0
          };
        });

        console.log(this.labelData, "labelData");
      });
    },
    //获取标签详情信息
    getLabelDetailData(data) {
      if (data) {
        this.labelDetailQueryForm.tagId = data.tagId;
        this.labelDetailQueryForm.size = 10;
        this.labelDetailQueryForm.current = 1;
      }
      let columnNameData = [];
      this.labelUserDisplay({ ...this.labelDetailQueryForm }).then(res => {
        this.labelDetailPageConfig.total = res.result[0]
          ? res.result[0].total
          : 0;
        this.labelDetailPageConfig.size = this.labelDetailQueryForm.size;
        this.labelDetailPageConfig.current = this.labelDetailQueryForm.current;
        if (res.showField[this.labelDetailQueryForm.tagId]) {
          columnNameData = res.showField[this.labelDetailQueryForm.tagId]
            .map(e => {
              return {
                prop: e.dbcolumnAS,
                label: e.dbcolumnTitle
              };
            })
            .filter(
              e =>
                e.label !== "地区编码" &&
                e.label !== "县区局编码" &&
                e.label !== "供电单位编码" &&
                e.label !== "供电电网编码"
            )
            .filter(
              e =>
                e.label !== "地区编码" &&
                e.label !== "县区局编码" &&
                e.label !== "供电单位编码" &&
                e.label !== "供电电网编码"
            );
        }
        console.log(columnNameData, "columnNameData");
        res.result.shift();
        this.labelDetailData = {
          tableData: [...res.result],
          columnNameData: columnNameData,
          name: "labelDetail",
          headerColor: "#1899fa",
          title: "客户明细列表",
          total: this.labelDetailPageConfig.total,
          current: this.labelDetailQueryForm.current,
          size: this.labelDetailQueryForm.size
        };
      });
    }
  }
};
</script>

<style scoped lang="scss">
::v-deep .el-table .warning-row {
  // background: #e7f6f7;
  background: #e9f3fe;
}
.panoramicAnalysis {
  width: 100%;
  height: 100%;
  font-size: 14px;
  box-sizing: border-box;
  .top,
  .center,
  .bottom {
    width: 100%;
    // height: calc(50% - 10px);
    height: 300px;
    box-sizing: border-box;
    .content-border {
      background: #fff;
      border: 2px solid rgba(26, 132, 244, 0.2);
      border-radius: 4px;
    }
    .content {
      width: 100%;
      height: 100%;
      display: flex;
      flex-wrap: wrap;
      .content-title {
        display: flex;
        align-items: center;
        height: 30px;
        line-height: 30px;
        padding: 5px 0px 0px 5px;
        font-weight: bold;
        color: #1a84f4;
        .block {
          display: inline-block;
          width: 8px;
          height: 16px;
          margin: 0 10px;
          background-image: linear-gradient(270deg, #17a8ff 0%, #1a84f4 100%);
        }
      }
      .chart-main {
        width: 100%;
        height: calc(100% - 30px);
        padding: 10px;
        box-sizing: border-box;
      }
      .content-left {
        width: calc(50% - 10px);
        margin-right: 10px;
        height: 100%;
      }
      .content-right {
        width: calc(50% - 10px);
        height: 100%;
      }
    }
  }
  .top {
    margin-bottom: 20px;
  }
  .center {
    margin-bottom: 20px;
  }
  .bottom {
    .content {
      display: flex;
      flex-wrap: wrap;
    }
  }
}
</style>
